.heading{
    position: relative;
}
.heading:after{
    content: "";
    position: absolute;
    top: 180%;
    left: 0;
    width: 50%;
    height: 3px;
    background-color: #207FDC;
}
.book{
    position: absolute;
    top: 2rem!important;
    right: 10%;
    z-index: 1;
}
.filled{
    top: 6rem!important;
}
@media all and (min-width: 320px) and (max-width: 768px){
    .book{
        position: relative!important;
        top: 3rem;
        right: 0;
        width: 100%!important;
    }
    .filled{
        margin-bottom: 7rem!important;
    }
}
@media all and (min-width: 769px) and (max-width: 1024px){
    .book{
        bottom: 0!important;
        margin-top: 0!important;
    }
    .heading{
        margin-bottom: 0!important;
    }
    .heading:after{
        top: 110%;
    }
}
